@use "../globals" as *;
@use "../mixins" as *;
@use "../helpers" as *;
@use "../components" as *;

/* -------- Start Sorting Section --------*/
.sort {
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    h2 {
      font-size: 20px;
      margin-bottom: 0;
    }
  }
}
/* --------- End Sorting Section ---------*/
/* ------- Start pagination Section ------*/
.pagination {
  margin-bottom: 20px;
  ul {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    li {
      margin: 5px;
      a {
        display: block;
        padding: 3px 10px;
        color: $main-clr;
        transition: $transition;
        border: 2px solid $main-clr;
        border-radius: $border-radius;
        &:hover {
          color: $white;
          background-color: $main-clr;
        }
      }
    }
  }
}
/* -------- End pagination Section -------*/
